/*
    1 - comman or tag css
    2 - header 

*/

body {
  font-family: "Poppins", sans-serif;
}
body::-webkit-scrollbar,
.scrollbar::-webkit-scrollbar {
  width: 0.5em;
}

body::-webkit-scrollbar-track,
.scrollbar::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

body::-webkit-scrollbar-thumb,
.scrollbar::-webkit-scrollbar-thumb {
  background-color: #ed1a3b;
  outline: 1px solid slategrey;
}

.scrollbar::-webkit-scrollbar-thumb {
  background-color: #fc8019;
  outline: 1px solid slategrey;
}

.user-dashboard h1,
.user-dashboard h2,
.user-dashboard h3,
.user-dashboard h4,
.user-dashboard h5,
.user-dashboard h6 {
  font-family: "Poppins", sans-serif;
}
/* Font Sizing */
.f-100 {
  font-size: 100%;
}
.f-110 {
  font-size: 110%;
}
.f-120 {
  font-size: 120%;
}
.f-130 {
  font-size: 130%;
}
.f-140 {
  font-size: 140%;
}
.f-150 {
  font-size: 150%;
}
.f-160 {
  font-size: 160%;
}
.f-170 {
  font-size: 170%;
}
.f-180 {
  font-size: 180%;
}
.f-190 {
  font-size: 190%;
}
.f-200 {
  font-size: 200%;
}

.user-dashboard a,
.user-dashboard .btn-link {
  text-decoration: none;
  text-decoration-color: #ed1a3b;
  text-decoration-style: dashed;
  color: #6c757d;
}
.user-dashboard a:hover,
.user-dashboard .dropdown-menu a:hover,
.user-dashboard .btn-link:hover {
  text-decoration: underline;
  text-decoration-color: rgba(237, 26, 61, 0.64);
  color: #ed1a3b;
}
.user-dashboard .container {
  max-width: 1200px;
}
.user-dashboard .btn-default {
  background: #ed1a3b;
  color: #fff;
}
.user-dashboard .overlay {
  z-index: 1100;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #282c3f;
  opacity: 0.7;
  overflow: hidden;
}

/* 2 - header========== */
.user-dashboard .categroy-icon svg {
  fill: #020202;
}
.user-dashboard .categroy-icon:hover svg {
  fill: #ed1a3b;
}
.user-dashboard .dropdown-toggle:after {
  content: none;
}

/* 2 - User Dashboard -------- index pager========== */
.user-dashboard .nav-link {
  background: #fff !important;
  border: 1px solid gray;
  transition: 0.3s ease-in-out;
}
.user-dashboard .nav-pills .active {
  background: #ed1a3b !important;
  color: #fff;
  transform: scale(1.05);
}
.user-dashboard .nav-link:hover {
  /* color: red; */
  /* transition: 1s; */
  background: #ed1a3b !important;
  color: #fff;
  transform: scale(1.05);
}

/* 2 - User Dashboard -------- footer========== */

.user-dashboard .search-v-btn {
  position: fixed;
  right: 0;
  top: 50%;
  z-index: 1100;
}
.user-dashboard .search-v-btn:hover {
  transition: 2s;
  transform: scale(1.8);
  background: #ed1a3b !important;
}
.user-dashboard .v-btn:hover {
  transition: 2s;
  transform: scale(1.4);
}

.user-dashboard .v-btn::after {
  content: "";
  display: block;
  border: 1px solid #fff;
}
.user-dashboard .search-listing {
  position: fixed;
  top: 0px;
  height: 400px;
  z-index: 1150;
}
.user-dashboard .search-listing ul {
  height: 300px;
  overflow: auto;
}
.user-dashboard .search-listing ul li {
  border-bottom: 2px solid #ed1a3b;
}
.user-dashboard .search-listing ul li:nth-child(odd) {
  background: rgba(128, 128, 128, 0.08);
}
.user-dashboard .btn-close-search-listing span {
  background: #ed1a3b;
  margin-top: 100px !important;
}

@media (max-width: 420px) {
  body {
    font-size: 80%;
  }
  .user-dashboard #logo img {
    width: 40%;
    height: 40%;
  }
}
