.datetimeblock .col div:hover {
  background: rgba(54, 111, 216, 0.54);
  color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
  -moz-box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
  -o-box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
  cursor: pointer;
}

.datetimeblock .col .disabled {
  border: 0px;
  background: lightgray;
}

.datetimeblock .col .disabled:hover {
  border: 0px;
  background: lightgray;
  color: #000;
  box-shadow: 0 0 0px rgba(0, 0, 0, 0.6);
  -moz-box-shadow: 0 0 0px rgba(0, 0, 0, 0.6);
  -webkit-box-shadow: 0 0 0px rgba(0, 0, 0, 0.6);
  -o-box-shadow: 0 0 0px rgba(0, 0, 0, 0.6);
  cursor: none;
}

.available:hover {
  cursor: pointer;
  /* color: #ed1a3b; */
  border: 1px solid black;
}
.active-cart-tabs {
  background: lightgreen;
  border: 1px solid black;
  font-weight: bold;
}
.disableTime {
  border: 1px solid black;
  color: #fff;
  background: gray;
  cursor: none;
}
.disableTime:hover {
  cursor: none;
}
.scrollable > ul,
.sub-cat > li > a {
  white-space: nowrap;
}
.stylist-img,
.stylist-img-add-cart {
  transition: 0.3s ease-in-out;
}
.stylist-img:hover,
.stylist-list:hover .stylist-img {
  /* -webkit-transform: scale(1.1); */
  height: 130px !important;
  width: 100%;
}
.stylist-list-add-cart:hover:hover {
  background: lightgreen;
}
