.container {
  max-width: 1200px;
}

.wowtis-header {
  /* overflow: hidden; */
  position: fixed;
  top: 0;
  width: 100%;
  background: #fff;
  /* min-height: 90px; */
  max-height: auto;
  z-index: 1000;
}
.wowtis-main-container {
  padding: 0px;
  margin-top: 100px;
}
.nav-shadow {
  -webkit-box-shadow: 0 20px 20px -10px #f7f2f2;
  -moz-box-shadow: 0 20px 20px -10px #f7f2f2;
  box-shadow: 0 20px 20px -10px #f7f2f2;
}
.wowtis-header-logo img {
  /* margin-top: 0px;
  height: 50px; */
  width: 300px;
  height: 50px;
}
.wowtis-header-logo span:first-child {
  font-size: 260%;
  color: #ed1a3b;
}
.wowtis-header-logo span:last-child {
  font-size: 200%;
  color: #000;
}
.wowtis-header-logo {
  text-decoration: none !important;
}

.category-icon svg:hover {
  /* fill: #8a3e6a; */
}

/* #category-popup {
  position: absolute;
  margin-top: 10px;
  height: 350px;
  background-color: #fff;
  display: none;
  z-index: 1000;
} */

#category-popup {
  position: absolute;
  /* margin-top: 0px; */
  margin: 0px auto;
  /* margin-left: 175px !important; */
  right: 0px;
  width: 280px;
  height: 350px;
  background-color: #fff;
  /* display: none; */
  z-index: 1000;
}

.overlay {
  z-index: 1000;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #282c3f;
  opacity: 0.7;
  overflow: hidden;
}

.image-cover {
  background: url("../../assets/img/rt.jpg") no-repeat center center;
  height: 400px;
  background-size: cover;
}
.image-cover form {
  top: 170px;
  margin: auto;
}
.has-search .form-control {
  padding-left: 2.375rem;
}

.has-search .form-control-feedback {
  position: absolute;
  /* z-index: 2; */
  display: block;
  width: 2.375rem;
  height: 2.375rem;
  line-height: 2.375rem;
  text-align: center;
  pointer-events: none;
  color: #e7d8d8;
}

/* =============List Page ======================= */
.list-card .card-img-top {
  height: 200px;
}
.quick-view-btn {
  clear: both;
  float: right;
  display: block;
  margin-top: 168px;
  position: relative;
  right: 2px;
  background: rgba(0, 0, 0, 0.66);
}
.list-card .card {
  height: 340px;
  min-width: 250px;
  max-width: 280px;
  overflow: hidden;
  -webkit-transition: width 2s;
  -moz-transition: width 2s;
  transition: width 2s;
}
.list-card .card:hover {
  border: 1px solid #063341;
}

.list-card .card-body {
  flex: 0 0 auto;
}

.popup-card {
  position: absolute;
  top: -30px;
  width: 390px;
  height: 390px;
  border: 1px solid rgba(34, 150, 150, 0.4);
  background: #fff;
  z-index: 2;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
  -moz-box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
  -webkit-box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
  -o-box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
}
.popup-card-left {
  right: 285px;
}
.popup-card-right {
  left: 270px;
  right: 0%;
}

.popup-card img {
  height: 75px;
}
article .card-header {
  background-color: #ed1a3b;
}
article .card-header h6 {
  color: #fff;
  font-weight: bold;
}
.sortby-icon {
  background: #ffd900;
  font-size: 140%;
  text-align: center;
  line-height: 50px;
  height: 50px;
  width: 50px;
  border-radius: 100%;

  /* padding: 10px; */
}

@media (max-width: 420px) {
  .owl-theme-cantainer {
    padding: 10px 10px !important;
    padding-top: 30px !important;
  }
}

/* ===============View Page =============*/
.nav-pills .nav-link {
  background: rgba(211, 211, 211, 0.32);
}
.nav-pills .nav-link.active {
  color: #fff;
  background-color: #ed1a3b;
}
.nav-pills .nav-link {
  border-radius: 0rem;
}
.view-carousel img {
  height: 300px;
  border: 1px solid red;
}

.checkbox {
  background: #ed1a3b !important;
  width: 110%;
}
.checkbox:hover {
  background: #ed1a3b;
}
.animated {
  -webkit-transition: height 0.2s;
  -moz-transition: height 0.2s;
  transition: height 0.2s;
}

.stars {
  margin: 20px 0;
  font-size: 24px;
  color: #d17581;
}
