.backToTop {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 45px;
  background-color: #ea7066;
  border: 2px solid #fff;
  border-radius: 13px;
  color: #fff;
  display: block;
  font-size: 16px;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=$value)";
  filter: alpha(opacity=0);
  -moz-opacity: 0;
  -khtml-opacity: 0;
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
@charset "UTF-8";
/**
 * On every reload, this pen generates random color and animation duration for each circle.
 * Play with variables to see the "magic" \_()_/
 * Thanks for coming and have a nice day! 
 */
.preloader-parent {
  z-index: 1000;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.85);
  overflow: hidden;
}
.preloader {
  opacity: 0.7;
  overflow: hidden;
  width: 100px;
  height: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0) scale3d(1, 1, 1);
  transform: translate3d(-50%, -50%, 0) scale3d(1, 1, 1);
  z-index: 10;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  cursor: pointer;
}
.preloader:hover {
  -webkit-transform: translate3d(-50%, -50%, 0) scale3d(0.8, 0.8, 0.8);
  transform: translate3d(-50%, -50%, 0) scale3d(0.8, 0.8, 0.8);
}
.preloader__circle {
  display: block;
  position: absolute;
  border: 1px solid rgba(245, 245, 245, 0);
  border-radius: 50%;
  -webkit-font-smoothing: subpixel-antialiased;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.preloader__circle:nth-child(1) {
  top: 1px;
  right: 1px;
  bottom: 1px;
  left: 1px;
  -webkit-animation: spinClockwise 2s infinite linear normal;
  animation: spinClockwise 2s infinite linear normal;
  border-right-color: #ff00eb;
}
.preloader__circle:nth-child(2) {
  top: 5px;
  right: 5px;
  bottom: 5px;
  left: 5px;
  -webkit-animation: spinClockwise 4s infinite linear normal;
  animation: spinClockwise 4s infinite linear normal;
  border-right-color: #00c9f9;
}
.preloader__circle:nth-child(3) {
  top: 9px;
  right: 9px;
  bottom: 9px;
  left: 9px;
  -webkit-animation: spinClockwise 6s infinite linear normal;
  animation: spinClockwise 6s infinite linear normal;
  border-bottom-color: #ff00eb;
}
.preloader__circle:nth-child(4) {
  top: 13px;
  right: 13px;
  bottom: 13px;
  left: 13px;
  -webkit-animation: spinClockwise 3s infinite linear normal;
  animation: spinClockwise 3s infinite linear normal;
  border-bottom-color: #00c9f9;
}
.preloader__circle:nth-child(5) {
  top: 17px;
  right: 17px;
  bottom: 17px;
  left: 17px;
  -webkit-animation: spinClockwise 5s infinite linear normal;
  animation: spinClockwise 5s infinite linear normal;
  border-left-color: #00c9f9;
}
.preloader__circle:nth-child(6) {
  top: 21px;
  right: 21px;
  bottom: 21px;
  left: 21px;
  -webkit-animation: spinClockwise 3s infinite linear normal;
  animation: spinClockwise 3s infinite linear normal;
  border-left-color: #ff00eb;
}
.preloader__circle:nth-child(7) {
  top: 25px;
  right: 25px;
  bottom: 25px;
  left: 25px;
  -webkit-animation: spinClockwise 4s infinite linear normal;
  animation: spinClockwise 4s infinite linear normal;
  border-top-color: #00c9f9;
}

/**
 * Animations
 */
@-webkit-keyframes spinClockwise {
  to {
    -webkit-transform: translate3d(0, 0, 0) rotate(360deg);
    transform: translate3d(0, 0, 0) rotate(360deg);
  }
}
@keyframes spinClockwise {
  to {
    -webkit-transform: translate3d(0, 0, 0) rotate(360deg);
    transform: translate3d(0, 0, 0) rotate(360deg);
  }
}
.app {
  font: 900 200px/1 "Poppins", sans-serif;
  font-size: calc(50px + (200 - 50) * ((100vw - 320px) / (1400 - 320)));
}
@media (max-width: 320px) {
  .app {
    font-size: 50px;
  }
}
@media (min-width: 1400px) {
  .app {
    font-size: 200px;
  }
}
.app::before {
  content: "Loading";
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-52%, -55%);
  transform: translate(-52%, -55%);
  letter-spacing: -0.05em;
  color: #f7f7f7;
}
.app::after {
  content: "";
  position: fixed;
  top: 20px;
  right: 20px;
  bottom: 20px;
  left: 20px;
  border: 20px solid #f7f7f7;
}
.google-covert {
  position: absolute;
  z-index: 1200;
  background: #fff;
}

