body {
  margin: 0px;
  padding: 0px;
  scroll-behavior: smooth;
  /* font-family: "Nunito", sans-serif; */
  /* font-family: "Montserrat", sans-serif; */
  /* font-family: "Arvo", serif; */
  /* font-family: "Lato", sans-serif; */
  /* font-family: "PT Sans", sans-serif; */
  font-family: "Poppins", sans-serif;
}
body::-webkit-scrollbar,
.scrollbar::-webkit-scrollbar {
  width: 0.5em;
}

body::-webkit-scrollbar-track,
.scrollbar::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

body::-webkit-scrollbar-thumb,
.scrollbar::-webkit-scrollbar-thumb {
  background-color: #ed1a3b;
  outline: 1px solid slategrey;
}

.scrollbar::-webkit-scrollbar-thumb {
  background-color: #fc8019;
  outline: 1px solid slategrey;
}
.scrollable {
  overflow-x: scroll;
  padding-left: 10px;
  padding-right: 10px;
}
.time-pills::-webkit-scrollbar {
  height: 10px;
  width: 2px;
}
.time-pills::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 6px #000;
  background-color: rgba(237, 26, 61, 0.74);
  border-radius: 10px;
}
.time-pills::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px #333;
  border-radius: 10px;
}
/* -================Login signip popup */
.right-side-popup-signup,
.right-side-popup-login,
.right-side-popup-edituserprofile,
.right-side-popup-viewservicedetails {
  position: fixed;
  top: 0;
  height: 100%;
  overflow-y: scroll;
  background: #fff;
  transition: transform 0.3s ease-out;
  z-index: 10001;
  /* display: none; */
  min-width: 420px;
  width: 400px;
  right: 0px;
  border-left: 2px solid #ed1a3b;
  border-bottom: 2px solid #ed1a3b;
}
.right-side-popup-singup .close span,
.right-side-popup-login .close span,
.right-side-popup-edituserprofile .close span,
.right-side-popup-viewservicedetails .close span {
  width: 150%;
}
.view-form-search input,
.view-form-search select {
  border: 0px;
  border-bottom: 1px solid rgba(128, 128, 128, 0.62);
  border-radius: 0px;
  /* border-radius: 2px 30px 2px; */
  /* height: 60px; */
}

.form-control:focus {
  /* border: 2px solid #ed1a3b; */
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.075);
}
.view-form-search input[type="submit"] {
  background: rgba(252, 127, 25, 0.69);
  color: #fff;
  font-weight: bold;
  font-size: 120%;
  /* border-radius: 30px; */
}

@media (max-width: 420px) {
  .right-side-popup-signup,
  .right-side-popup-login,
  .right-side-popup-edituserprofile,
  .right-side-popup-viewservicedetails {
    width: 250px !important;
  }
}

.cursor-pointer {
  cursor: pointer;
}
