.form-signin {
  width: 100%;

  padding: 15px;
  margin: auto;
}

.form-signin .form-label-group {
  position: relative;
  margin-bottom: 1rem;
}

.form-signin .form-label-group > input,
.form-signin .form-label-group > label,
.form-signin .form-label-group > select {
  height: 3.125rem;
  padding: 0.75rem;
}

.form-signin .form-label-group > label {
  position: absolute;
  top: 0;
  left: 10;
  display: block;
  width: 100%;
  margin-bottom: 0;
  /* Override default `<label>` margin */
  line-height: 1.5;
  color: #495057;
  pointer-events: none;
  cursor: text;
  /* Match the input under the label */
  border: 1px solid transparent;
  border-radius: 0.25rem;
  transition: all 0.1s ease-in-out;
}

.form-signin .form-label-group input::-webkit-input-placeholder {
  color: transparent;
}

.form-signin .form-label-group input:-ms-input-placeholder {
  color: transparent;
}

.form-signin .form-label-group input::-ms-input-placeholder {
  color: transparent;
}

.form-signin .form-label-group input::-moz-placeholder {
  color: transparent;
}

.form-signin .form-label-group input::placeholder {
  color: transparent;
}

.form-signin .form-label-group input:not(:placeholder-shown) {
  padding-top: 1.25rem;
  padding-bottom: 0.25rem;
}

.form-signin .form-label-group input:not(:placeholder-shown) ~ label {
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  font-size: 12px;
  color: #777;
}
/* Fallback for Edge
    -------------------------------------------------- */

@supports (-ms-ime-align: auto) {
  .form-signin .form-label-group > label {
    display: none;
  }
  .form-signin .form-label-group input::-ms-input-placeholder {
    color: #777;
  }
}
/* Fallback for IE
    -------------------------------------------------- */

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .form-signin .form-label-group > label {
    display: none;
  }
  .form-signin .form-label-group input:-ms-input-placeholder {
    color: #777;
  }
}
