.owl-theme-cantainer {
  margin-top: 105px;
  height: auto;
  background-color: #282c3f;
}

.owl-theme-img img {
  height: 250px;
}

.owl-theme-img .item {
  transition: all 0.5s ease-in-out;
}

.owl-theme-img .item:hover {
  -ms-transform: scale(1.1);
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.modal {
  z-index: 10000;
}
.display-block {
  display: block !important;
}
.display-none {
  display: none !important;
}
