body::-webkit-scrollbar {
  width: 0.5em;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

body::-webkit-scrollbar-thumb {
  background-color: #ed1a3b;
  outline: 1px solid slategrey;
}

.wowtis-header-logo img {
  /* margin-top: 0px;
    height: 50px; */
  width: 300px;
  height: 50px;
}
.wowtis-header-logo span:first-child {
  font-size: 260%;
  color: #ed1a3b;
}
.wowtis-header-logo span:last-child {
  font-size: 200%;
  color: rgba(0, 0, 0, 0.68);
}
.wowtis-header-logo {
  text-decoration: none !important;
}
.category-icon:hover {
  fill: #ed1a3b;
  color: #ed1a3b;
}
#category-popup-index {
  position: absolute;
  margin-top: 0px !important;
  margin-right: 150px !important;
  /* margin-left: -1277770px !important; */
  width: 280px;
  height: 350px;
  background-color: #fff;
  /* display: none; */
  z-index: 1200;
  cursor: pointer;
}

.overlayTest {
  z-index: 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(40, 44, 63, 0.21);
  opacity: 0.7;
  overflow: hidden;
  cursor: pointer;
}

.image-cover {
  background: url("../../assets/img/rt.jpg") no-repeat center center;
  background-size: cover;
  bottom: 0px;
}
.image-cover form {
  top: 170px;
  margin: auto;
}
.has-search .form-control {
  padding-left: 2.375rem;
}

.has-search .form-control-feedback {
  position: absolute;
  /* z-index: 2; */
  display: block;
  width: 2.375rem;
  height: 2.375rem;
  line-height: 2.375rem;
  text-align: center;
  pointer-events: none;
  color: #e7d8d8;
}
.btn-find {
  width: 140px;
}
.login-signup-btn button {
  font-size: 105%;
}
.login-signup-btn button:hover,
.btn-find:hover {
  color: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.36);
  -moz-box-shadow: 0 0 5px rgba(0, 0, 0, 0.36);
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.36);
  -o-box-shadow: 0 0 5px rgba(0, 0, 0, 0.36);
}

.locate-me {
  position: absolute;
  z-index: 1;
  line-height: 55px;
  right: 145px;
}
.locate-me button {
  text-decoration: none;
  color: #0e0c0c;
}

.locate-me button:hover {
  text-decoration: none;
  color: rgba(14, 12, 12, 0.63);
  background: rgba(128, 128, 128, 0.22);
}

#searchbox {
  height: 60px;
}
#searchbox:focus {
  border-color: #ed1a3b;
}
.front-footer-img {
  height: 180px;
  overflow: hidden;
  text-align: center;
}
.front-footer-img-caption {
  background: rgba(237, 26, 61, 0.6);
  color: #fff;
  position: relative;
  font-weight: bold;
  top: 110px;
  text-align: center;
  width: 100% !important;
  font-size: 25px;
}
.front-footer-img:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
  -moz-box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
  -o-box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
  -ms-transform: scale(1.01);
  /* IE 9 */
  -webkit-transform: scale(1.01);
  /* Safari */
  transform: scale(1.01);
}
.dropdown:hover .dropdown-menu {
  display: block;
  margin-top: 0;
}

@media (min-width: 768px) {
  .bd-placeholder-img-lg {
    font-size: 3.5rem;
  }
  .category-icon {
    padding-top: 20px !important;
  }
}

@media (max-width: 840px) {
  .category-icon {
    padding-top: 20px !important;
  }
  .wowtis-header-logo span:first-child {
    font-size: 200%;
  }
  .wowtis-header-logo span:last-child {
    font-size: 180%;
  }
}

@media (max-width: 716px) {
  .wowtis-header-logo {
    line-height: 50px;
  }
  .wowtis-header-logo span:first-child {
    font-size: 150%;
  }
  .wowtis-header-logo span:last-child {
    font-size: 130%;
  }
  .category-icon {
    font-size: 50%;
  }
  .login-signup-btn button {
    font-size: 80%;
  }
  .userIconUseLogin {
    visibility: visible;
  }
}

@media (max-width: 420px) {
  .content-section h1 {
    font-size: 195% !important;
  }
  .content-section div:first-child {
    margin-top: 10px !important;
  }

  .content-section div:nth-child(2) {
    margin-top: 0px !important;
  }

  .content-section h3 {
    font-size: 130% !important;
  }
  .content-section {
    padding: 0px !important;
    margin: 0px !important;
  }
  .category-icon {
    text-align: center !important;
  }
  #category-popup {
    margin-right: 0px !important;
  }
  .btn-find {
    height: 50px;
    width: 90%;
  }
  #searchbox {
    height: 50px;
  }
  .locate-me button {
    margin-right: -80px !important;
    margin-top: -27px;
    position: relative;
    right: -20px;
  }
  .set-p4-mobile-view {
    padding: 1.5rem !important;
  }
}
